.grid-element {
  display: flex;
  height: 170px;
  width: 170px;
  background-color: rgba(0, 0, 0);
  background-size: cover;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: Open Sans, Lucida Grande, Helvetica Neue, Helvetica, Arial,
    Sans-serif;
  margin-bottom: 10px;
}
.tags-container1 {
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
}
.tags-container2 {
  flex-wrap: wrap-reverse;
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
}

.artists-container {
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
}

.top-artist {
  text-align: center;
  height: 530px;
  width: 570px;
  background-size: cover;
  margin-bottom: 10px;
  background-repeat: no-repeat;
}

.top-artist1-section1 {
  text-align: center;
  height: 530px;
  width: 570px;
  background-size: cover;
  margin-bottom: 10px;
  background-repeat: no-repeat;
  background-color: black;
}

.top-artist2-section1 {
  margin-left: 15px;
  text-align: center;
  height: 530px;
  width: 570px;
  background-size: cover;
  margin-bottom: 10px;
  background-repeat: no-repeat;
  background-color: black;
}

.artist-name {
  font-weight: bold;
  font-size: 14pt;
}
.bio {
  opacity: 0.8;
  height: 170px;
}
.artist-one-two {
  margin-bottom: 10px;
}
.top-artist1 {
  margin-right: 15px;
}

.top-artist2 {
  margin-left: 15px;
}

.section-title {
  color: black;
  display: flex;
  font-weight: bold;
  font-size: 15pt;
  margin: 30px;
  font-family: Open Sans, Lucida Grande, Helvetica Neue, Helvetica, Arial,
    Sans-serif;
  justify-content: center;
}

.top-artist {
  text-align: left;
  display: flex;
  align-items: flex-end;
}

.artist-info {
  font-family: Open Sans, Lucida Grande, Helvetica Neue, Helvetica, Arial,
    sans-serif;
  padding: 30px;
  font-size: 12pt;
  color: white;
}

.bio {
  font-size: 11pt !important;
}
.tag-link {
  font-size: 16pt;
  font-weight: bold;
  color: white !important;
}
.title {
  flex-direction: column;
  display: flex;
  justify-content: center;
}

.title-string {
  margin-bottom: 10px;
}

.line {
  margin-top: 10px;
  left: 50%;
  right: 50%;
  height: 3px;
  width: 80px;
  margin-left: auto;
  margin-right: auto;
  background-color: red;
}

@media (max-width: 1160px) {
  .top-artist1 {
    margin-right: 5px;
  }

  .top-artist2 {
    margin-left: 5px;
  }
  .top-artist2-section1 {
    margin-left: 5px;
  }
}

@media (max-width: 1150px) {
  .top-artist1 {
    margin-right: 2px;
  }

  .top-artist2 {
    margin-left: 2px;
  }
  .top-artist2-section1 {
    margin-left: 2px;
  }
}

.loader-container {
  height: 75vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.read {
  background: none !important;
  border: none !important;
  padding: 0 !important;
  font-weight: 550;
  font-family: Open Sans, Lucida Grande, Helvetica Neue, Helvetica, Arial,
    Sans-serif;
  text-decoration: underline;
  cursor: pointer;
}

.read:hover {
  color: #1890ff;
}

#myFooter {
  background-color: black;
  color: white;
  padding-top: 30px;
  font-family: Open Sans, Lucida Grande, Helvetica Neue, Helvetica, Arial,
    Sans-serif;
}

#myFooter .footer-copyright {
  background-color: black;
  padding-top: 3px;
  padding-bottom: 3px;
  text-align: center;
}

#myFooter .row {
  margin-bottom: 60px;
}

#myFooter .navbar-brand {
  margin-top: 45px;
  height: 65px;
}

#myFooter .footer-copyright p {
  margin: 10px;
  color: #ccc;
}

#myFooter ul {
  list-style-type: none;
  padding-left: 0;
  line-height: 1.7;
}

#myFooter h5 {
  font-size: 18px;
  color: white;
  margin-top: 30px;
  font-weight: 300;
}

#myFooter h2 a {
  font-size: 50px;
  text-align: center;
  color: #fff;
}

#myFooter a {
  color: #d2d1d1;
  text-decoration: none;
}

#myFooter a:hover,
#myFooter a:focus {
  text-decoration: none;
  color: white;
}

#myFooter .social-networks {
  text-align: center;
  padding-top: 30px;
  padding-bottom: 16px;
  color: white;
}

#myFooter .social-networks a {
  font-size: 32px;
  color: #f9f9f9;
  padding: 10px;
  transition: 0.2s;
}

#myFooter .social-networks a:hover {
  text-decoration: none;
}

#myFooter .facebook:hover {
  color: #0077e2;
}

#myFooter .google:hover {
  color: #ef1a1a;
}

#myFooter .twitter:hover {
  color: #00aced;
}

#myFooter .btn {
  color: white;
  background-color: red;
  border-radius: 20px;
  border: none;
  width: 150px;
  display: block;
  margin: 0 auto;
  margin-top: 10px;
  line-height: 25px;
}

@media screen and (max-width: 767px) {
  #myFooter {
    text-align: center;
  }
}

html {
  height: 100%;
}

body {
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  -webkit-flex-direction: column;
  height: 100%;
}

.content {
  flex: 1 0 auto;
  -webkit-flex: 1 0 auto;
  min-height: 200px;
}

#myFooter {
  flex: 0 0 auto;
  -webkit-flex: 0 0 auto;
}

.logo-img {
  height: 50px;
  width: 200px;
  margin-top: 25px;
  margin-left: 25px;
}

.header-logo {
  height: 30px;
  width: 120px;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: black;

  text-align: center;
  height: 52px;
  color: white;
}

.icons {
  text-align: left;
  position: relative;
  width: 45%;
  display: flex;
  justify-content: flex-start;
  /* margin-right: 95px; */
}
.music-icons {
  font-size: 25pt;
  color: white;
  margin: 10px;
}

.music-icons:hover {
  color: red;
}

.search-icon {
  font-size: 18pt;
  color: white;
  padding: 10px;
}

.header-menu {
  position: relative;
  width: 45%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 11.5pt;
  font-weight: 440;
}

.header-center {
  display: flex;
  justify-content: center;
}

.menu-item {
  padding: 13px;
  color: white;
  text-decoration: none;
  font-family: Open Sans, Lucida Grande, Helvetica Neue, Helvetica, Arial,
    Sans-serif;
}

.menu-item:hover {
  color: red;
  border-bottom: solid red;
  border-top: solid black;
  border-bottom-width: 4px;
  border-top-width: 4px;
}

.search-icon:hover {
  color: red;
  border-bottom: solid red;
  border-top: solid black;
  border-bottom-width: 4px;
  border-top-width: 4px;
  cursor: pointer;
}

.search-bar {
  position: relative;
  width: 95%;
}

.search-clicked-icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  width: 5%;
}
.search-input {
  position: relative;
  background-color: black;
  border: none !important;
  height: 52px;
  width: 100%;
  font-size: 14pt;
  font-family: Open Sans, Lucida Grande, Helvetica Neue, Helvetica, Arial,
    Sans-serif;
  padding: 10px;
}
.search-input:focus {
  outline: none !important;
}

.search-icon::placeholder {
  color: #666666;
}

.search-clicked {
  text-align: center;
  position: relative;
  width: 50%;
  font-size: 17pt;
  cursor: pointer;
  padding: 5px;
  opacity: 0.75;
}

.closed {
  text-align: center;
  position: relative;
  width: 50%;
  color: white;
  font-size: 17pt;
  opacity: 0.75;
  padding: 5px;
}

.search-clicked:hover {
  color: red;
  border-bottom: solid red;
  border-top: solid black;
  border-bottom-width: 4px;
  border-top-width: 4px;
  cursor: pointer;
  opacity: 1;
}
.closed:hover {
  color: red;
  border-bottom: solid red;
  border-top: solid black;
  border-bottom-width: 4px;
  border-top-width: 4px;
  cursor: pointer;
  opacity: 1;
  cursor: pointer;
}

#contact-us {
  outline: none;
}
